* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Klee One", cursive;
}

#root {
  display: flex;
  min-height: 100vh;
  width: 100%;
  align-items: stretch;
  flex: 1;
}

#subnav {
  position: sticky;
  top: 0;
  background: white;
}

#sidebar {
  position: sticky;
  top: 0;
  height: 100vh;
}
